@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.container {
  max-width: 320px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

button {
  cursor: pointer;
  background: #FFFFFF;
  border: 2px solid #444444;
}

h1 {
  font-weight: @bold;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
}

h2 {
  font-weight: @bold;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}

h3 {
  font-weight: @bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
